.modal-container {
  width: 100%;
}

.modal-header {
  width: 100%;
  padding: 0;
  margin: 0 0 20px 0;
  height: 50px;
  border: none;
}

.modal-main-msg {
  display: inline-block;
  padding: 0 0 0 10px;
  margin: 0;
  vertical-align: middle;
}

#modal-img {
  display: inline-block;
  height: 100%;
}

.modal-sub-container {
  min-height: 40px;
  max-width: 250px;
}

.modal-sub-msg {
  display: block;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 16px;
  text-shadow: 2px 2px 8px gray;
  color: red;
}

.modal-ok {
  float: right;
}

hr {
  margin: 15px 0 10px 0;
  border-top-color: darkgray;
}


